<template>
  <div class="active-dynamic-container">
    <van-search
      v-model="keyword"
      placeholder="请输入搜索关键词"
      @search="onSearch"
      @clear="onClear"
    />
    <van-tabs v-model="activeTab" animated @change="changeTabs">
      <van-tab title="全部">
        <ul class="list-style">
          <li
            v-for="item in activeDynamicList"
            :key="item.id"
            @click="goToDetail(item)"
          >
            <ActiveRow :data="item" class="active-row-style" />
          </li>
        </ul>
      </van-tab>
      <van-tab title="已备案">
        <ul class="list-style">
          <li
            v-for="item in listBeforeCertainTime"
            :key="item.id"
            @click="goToDetail(item)"
          >
            <ActiveRow :data="item" class="active-row-style" />
          </li>
        </ul>
        <div class="list-style"></div>
      </van-tab>
      <van-tab title="已提交">
        <ul class="list-style">
          <li
            v-for="item in listAfterCertainPeriodTime"
            :key="item.id"
            @click="goToDetail(item)"
          >
            <ActiveRow :data="item" class="active-row-style" />
          </li>
        </ul>
      </van-tab>
    </van-tabs>
  </div>
</template>

<script>
import { handleSearch } from "@/utils/common";

export default {
  components: {
    ActiveRow: () => import("./components/ActiveRow"),
  },
  data() {
    return {
      keyword: "",
      activeTab: null,
      // 分页参数
      page: {
        // 每页显示条数
        limit: 999999999,
        // 当前页码
        pageNo: 1,
      },
      // 工会ID
      aid: null,
      // 活动动态列表
      activeDynamicList: [],
      // 讲座模块-某个时间段前的列表(活动列表)API
      listBeforeCertainTime: [],
      // 讲座模块-某个时间段后的列表(活动列表)API
      listAfterCertainPeriodTime: [],
    };
  },
  async created() {
    this.aid = this.$store.getters.associationInfoCurrent.id;
    this.initListData();
  },
  methods: {
    initListData() {
      this.listLectureAPI(this.aid, this.page.pageNo, this.page.limit);
      this.listBeforeCertainTimeAPI(
        this.aid,
        this.page.pageNo,
        this.page.limit,
        this.$dateFormat(new Date(), "yyyy-mm-dd")
      );
      this.listAfterCertainPeriodTimeAPI(
        this.aid,
        this.page.pageNo,
        this.page.limit,
        this.$dateFormat(new Date(), "yyyy-mm-dd")
      );
    },
    /**
     * 当前激活的标签改变时触发
     */
    changeTabs(params) {
      console.log("params :>> ", params);
      switch (params) {
        case 0:
          this.listLectureAPI(this.aid, this.page.pageNo, this.page.limit);
          break;
        case 1:
          this.listBeforeCertainTimeAPI(
            this.aid,
            this.page.pageNo,
            this.page.limit,
            this.$dateFormat(new Date(), "yyyy-mm-dd")
          );
          break;
        case 2:
          this.listAfterCertainPeriodTimeAPI(
            this.aid,
            this.page.pageNo,
            this.page.limit,
            this.$dateFormat(new Date(), "yyyy-mm-dd")
          );
          break;

        default:
          console.log("Error :>> ", "changeTabs参数异常");
          break;
      }
    },

    /**
     * 讲座模块-获取列表(活动列表)API
     * @param {String} aid 工会ID
     * @param {Number} page 当前页码
     * @param {Number} limit 每页显示条数
     * @param {Date} start_date 当前日期
     */
    async listLectureAPI(aid, page, limit, start_date) {
      const data = {
        aid,
        page,
        limit,
        start_date,
      };
      const res = await this.$request("lecture.listLecture", data);
      console.log("讲座模块-获取列表(活动列表)API_res :>> ", res);
      this.activeDynamicList = res;
    },
    /**
     * 讲座模块-某个时间段前的列表(活动列表)API
     * @param {String} aid 工会ID
     * @param {Number} page 当前页码
     * @param {Number} limit 每页显示条数
     * @param {Date} start_date 当前日期
     */
    async listBeforeCertainTimeAPI(aid, page, limit, start_date) {
      const data = {
        aid,
        page,
        limit,
        start_date,
      };
      const res = await this.$request("lecture.listBeforeCertainTime", data);
      console.log("讲座模块-某个时间段前的列表(活动列表)API_res :>> ", res);
      this.listBeforeCertainTime = res;
    },
    /**
     * 讲座模块-某个时间段后的列表(活动列表)API
     * @param {String} aid 工会ID
     * @param {Number} page 当前页码
     * @param {Number} limit 每页显示条数
     * @param {Date} start_date 当前日期
     */
    async listAfterCertainPeriodTimeAPI(aid, page, limit, start_date) {
      const data = {
        aid,
        page,
        limit,
        start_date,
      };
      const res = await this.$request(
        "lecture.listAfterCertainPeriodTime",
        data
      );
      console.log("讲座模块-某个时间段后的列表(活动列表)API_res :>> ", res);
      this.listAfterCertainPeriodTime = res;
    },
    /**
     * 跳转活动详情页面
     */
    goToDetail(param) {
      console.log("param :>> ", param);
      this.$router.push({
        path: `/activeDynamicDetails`,
        query: {
          id: param.id,
        },
      });
    },
    /**
     * 确定搜索时触发
     */
    onSearch() {
      this.activeDynamicList = handleSearch(
        this.activeDynamicList,
        this.keyword,
        "title"
      );

      this.listBeforeCertainTime = handleSearch(
        this.listBeforeCertainTime,
        this.keyword,
        "title"
      );

      this.listAfterCertainPeriodTime = handleSearch(
        this.listAfterCertainPeriodTime,
        this.keyword,
        "title"
      );

      if (this.keyword === "") {
        this.initListData();
      }
    },
    /**
     * 点击清除按钮后触发
     */
    onClear() {
      console.log("onClear :>> ");
      this.initListData();
    },
  },
};
</script>

<style lang="scss" scoped>
.active-dynamic-container {
  .list-style {
    padding: px(24);
    .active-row-style {
      margin: 0 0 px(24);
    }
  }
}
</style>
